.double-tab-container {
  & > ul {
    position: relative;
    clear: both;
    width: 100%;
    white-space: nowrap;
    overflow-x: auto;
    padding: 0;
    -ms-overflow-style: none;
    scrollbar-width: none;
    display: flex;
    gap: rem(12px);
    margin-bottom: rem(12px);

    &::-webkit-scrollbar {
      display: none;
    }
  }
}
