@mixin auto {
    width: auto;
    position: relative;
}
.skyplus-design-system-country-dropdown-wrapper {
   @include auto;
   border-radius: 8px;
   border: 1px solid  #E2EBF2;
   background: #fff;
   height: 45px;
   .allow-dropdown {
    display: block !important;
    @include auto;
    .flag-container {
        @include auto;
        .selected-flag {
            display: flex;
            align-items: center;
            background: none !important;
            justify-content: flex-start;
            margin-top: 10px;
            .iti-flag {
                margin: 0 8px;
            }
            .selected-dial-code {
                padding-left: 0 !important;
                color: #25304B;
                font-size: 14px;
                font-weight: 300;

            }
            .arrow {
                display: none;
            } 
        }     
    }
    input {
        display: none;
    }
   }
}