@mixin fixedConent() {
  background-color: #ffffff;
}

body::-webkit-scrollbar {
  display: none;
}

.scrollable-body::-webkit-scrollbar {
  display: none;
}

// @-webkit-keyframes slide {
//   100% {
//     right: 0;
//   }
// }

@keyframes slide {
  100% {
    right: 0;
  }
}

.skyplus-slider {
  &__body {
    top: 0;
    height: 100%;
    width: 100%;
    position: fixed;

    @include media('<tablet') {
      left: 0;
    }

    @include media('>=tablet') {
      position: absolute;
      right: -441px;
      width: 441px;
      height: 100%;
      box-shadow: -2px 0 5px rgba(0, 0, 0, 0.5);
      animation: slide 0.5s forwards;
      animation-delay: 0.3s;
    }
  }
  &__header {
    width: 100%;
    top: 0;
    left: 0;
    height: rem(50px);
    box-shadow: 0px 0px 12px 0px #4c5d9e14;
    @include fixedConent();
    @include media('>=tablet') {
      padding: rem(12px), rem(16px), rem(12px), rem(16px);
    }
  }

  &__header-close-icon {
    padding: rem(12px);
    display: flex;
    height: rem(25px);
    width: rem(24px);
    cursor: pointer;
    color: #24409a;
  }

  &__body-content {
    overflow-y: auto;
    width: 100%;
    height: calc(100vh - 50px);
    @include media('>=tablet') {
      height: calc(100vh - 50px);
    }
    padding: rem(12px);
  }

  &__body-content-with-footer {
    overflow-y: auto;
    width: 100%;
    height: calc(100vh - (50px + 134px));
    @include media('>=tablet') {
      height: calc(100vh - (50px + 134px));
    }
    padding: rem(12px);
  }

  &__footer {
    bottom: 0;
    left: 0;
    height: rem(134px);
    width: 100%;
    min-height: rem(16px);
    border-radius: rem(12px) rem(12px) rem(0px) rem(0px);
    box-shadow: 0px -5px 5px rgba(0, 0, 0, 0.3);
    @include media('>=tablet') {
      height: rem(148px);
      padding: rem(16px), rem(20px), rem(24px), rem(20px);
      border-radius: rem(12px), rem(12px), rem(0px), rem(0px);
    }
    @include fixedConent();
  }

  &__footer-content {
    padding: rem(16px) rem(20px) rem(24px) rem(20px);
  }

  &__footer-details {
    display: flex;
    justify-content: space-between;
  }
  &__foote-title {
    font-size: rem(14px);
    line-height: rem(20px);
    margin-bottom: rem(4px);
    font-weight: 600;
    color: #25304b;
    @include media('>=tablet') {
      font-family: Poppins;
      font-size: rem(16px);
      font-weight: 600;
      line-height: rem(24px);
      letter-spacing: rem(0px);
      text-align: left;
    }
  }
  &__footer-sub-title {
    font-size: rem(10px);
    font-weight: 400;
    line-height: rem(16px);
    color: #7a85a0;
    text-align: right;
    &--line-through {
      text-decoration: line-through;
    }
    @include media('>=tablet') {
      font-family: Poppins;
      font-size: rem(12px);
      font-weight: 400;
      line-height: rem(18px);
      letter-spacing: rem(0px);
      text-align: left;
    }
  }
  &__button {
    padding-top: rem(4px);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__button-icon {
    padding-right: rem(6px);
  }
}
